.header {
    background: -webkit-linear-gradient(90deg, #090f30, #000000, #190631);
    background: linear-gradient(90deg, #090f30, #000000, #190631);
    padding: 2em 0;
}

.header__container {
    display: flex;
    justify-content: space-between;
}
.header__logo {
    display: flex;
    align-items: center;
}

.header__logo-link {
    @include font-patterns(#ff7846, "Poppins-Regular", 2rem, normal, 400, 62%);
    &:hover {
        cursor: pointer;
    }
}

.header__nav {
    display: flex;
    align-items: center;
}
.header__nav-list {
    display: flex;
    column-gap: 4em;
}

.header__nav-link,
.header__burger-link {
    @include font-patterns(#ff7846, "TheCrewPro", 1.25rem, normal, 700, 100%);
    cursor: pointer;

    &:hover {
        color: lightgray;
    }
}

.header__burger-link {
    @include font-patterns(#ff7846, "TheCrewPro", 2rem, normal, 700, 100%);
}

.active {
    color: #fff !important;
}

.header__contacts {
    display: flex;
    column-gap: 1.5em;
}

.header__contacts-item {
}

.header__contacts-link {
    img {
        transition: 0.2s;
    }
    &:hover {
        cursor: pointer;
        img {
            transform: rotateX(360deg);
            transition: 0.2s;
        }
    }
}

// Burger menu

.burger-button {
    display: none;
    position: fixed;
    right: 2vh;
    z-index: 9;
    padding: 8px;

    span {
        display: block;
        width: 40px;
        height: 4px;
        background-color: #fff;
        border-radius: 22px;
        transition: 0.5s;
    }

    &.active {
        span {
            background-color: #fff;
        }

        .burger-top {
            position: absolute;
            top: 8px;
            transform: rotateZ(45deg);
        }

        .burger-middle {
            background-color: rgba(0, 0, 0, 0);
            border-color: rgba(0, 0, 0, 0);
        }

        .burger-bottom {
            position: absolute;
            bottom: 8px;
            transform: rotateZ(-45deg);
        }
    }
}

.burger-top,
.burger-bottom {
    position: absolute;
}

.burger-top {
    top: -2px;
}

.burger-bottom {
    bottom: -2px;
}

.burger-menu {
    display: flex;
    align-items: center;
    position: fixed;
    top: 0vh;
    left: 100vw;
    right: -100vw;
    bottom: 0vh;
    background-color: black;
    padding: 5px;
    box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.1);
    z-index: 8;
    transition: 0.3s;
    align-content: space-around;
    justify-content: space-around;
    flex-direction: column;

    &.active {
        left: 0vw;
        right: 0;
        opacity: 0.97;
    }
}

/*  MEDIA STYLES WILL BE HERE  */
@media (max-width: 1399px) {
    .header__contacts {
        display: none;
    }

    .header__container {
        justify-content: space-around;
    }
}

@media (max-width: 1280px) {
    .header__container {
        justify-content: space-between;
    }
}

@media (max-width: 1280px) {
    .header__container {
        justify-content: space-between;
    }
}

@media (max-width: 1200px) {
    .header__container {
        justify-content: space-around;
    }

    .header__nav-list {
        column-gap: 2.5em;
    }
}

@media (max-width: 1024px) {
    .burger-button {
        display: block;
    }

    .header__nav-list {
        display: none;
    }

    .header__container {
        justify-content: space-between;
    }
}

@media (max-width: 500px) {
    .header__burger-link {
        @include font-patterns(#ff7846, "TheCrewPro", 1.75rem, normal, 700, 100%);
    }
}
