/* IMPORT ALL STYLES AND MIXINS HERE  */
@import "./components/normalize";
@import "./components/_vars";
@import "./components/_mixins";
@import "./components/_header";
@import "./components/_footer";
@import "./components/_defaults";
@import "./components/_fonts";

.motivation {
  background: -webkit-linear-gradient(90deg, #090f30, #000000, #190631);
  background: linear-gradient(90deg, #090f30, #000000, #190631);
}

.motivation__container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  min-height: calc(100vh - 104px);
}

.motivation__desc {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  row-gap: 5em;
}

.motivation__desc-title {
  @include font-patterns(#f0f0f0, "Lordcorps", 5rem, normal, 500, 91%);
  text-transform: uppercase;
}
.motivation__desc-text {
  @include font-patterns(#f0f0f0, "Inter-Regular", 1.25rem, normal, 400, 170%);
  max-width: 500px;
}

.motivation-desc-consultation {
  @include font-patterns(#fff, "TheCrewPro", 1.5rem, normal, 700, 170%);
  letter-spacing: 0.1em;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f54e4e;
  width: 100%;
  height: 71px;
  border: 4px solid #f54e4e;
  border-radius: 40px;
  margin: 0 auto;
  padding: 15px 25px;
  text-align: center;
  transition: 0.2s;

  &:hover {
    background-color: #fff;
    border: 4px solid #fff;
    color: #000;
    transition: 0.2s;
  }
}

.motivation__picture {
  padding: 0 0 8em 0;
}

.mission {
  background: -webkit-linear-gradient(90deg, #090f30, #000000, #190631);
  background: linear-gradient(90deg, #090f30, #000000, #190631);
  padding: 0 0 8.75em 0;
}

.mission__subtitle {
  @include font-patterns(#f0f0f0, "Inter-Regular", 1.5rem, normal, 400, 170%);
  max-width: 50%;
  margin: 0 auto;
  text-align: center;
}

.mission__img {
  display: inline-block;
  padding: 1em 0 0.5em 0;
  margin: 0 auto;
  filter: invert(1);
}

.coaches {
  background: -webkit-linear-gradient(90deg, #090f30, #000000, #190631);
  background: linear-gradient(90deg, #090f30, #000000, #190631);
  padding: 0 0 8.75em 0;
}

.coaches__title {
  @include font-patterns(#f0f0f0, "HandveticaNeue", 2.5rem, normal, 500, 170%);
  padding: 0.5em 0;
  text-align: center;
  text-transform: uppercase;
}

.coaches__list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  column-gap: 12px;
  row-gap: 2em;
}

.coaches__list-item {
  img {
    display: block;
    margin: 0 auto;
    border-radius: 1em;
    transition: 0.2s;
  }
}

.coaches__list-item:hover {
  h3 {
    filter: invert(0.5);
    transition: 0.2s;
  }
  img {
    transform: scale(0.98);
    transition: 0.2s;
  }
}

.coaches__list-item > *:hover {
  cursor: pointer;
}

.coaches__item-link {
}

.coaches__link-title {
  @include font-patterns(#f0f0f0, "TheCrewPro", 1.5rem, normal, 500, 170%);
  text-align: center;
  transition: 0.2s;
}
.coaches__link-subtitle {
  @include font-patterns(#f0f0f0, "Inter-Regular", 1rem, normal, 400, 170%);
  text-align: center;
}

.path {
  background: -webkit-linear-gradient(90deg, #090f30, #000000, #190631);
  background: linear-gradient(90deg, #090f30, #000000, #190631);
  padding: 0 0 8.75em 0;
}

.path__title,
.mission__title {
  @include font-patterns(#f0f0f0, "HandveticaNeue", 2.5rem, normal, 500, 170%);
  padding: 0.5em 0;
  text-align: center;
  text-transform: uppercase;
}

.path__list {
  display: flex;
  justify-content: center;
  column-gap: 5em;
}

.path__list-item {
  text-align: center;
  filter: invert(1);
  transition: 0.2s;
}

.path__item-link {
  display: block;
}

.path__item-title {
  @include font-patterns(#000, "TheCrewPro", 1.25rem, normal, 500, 170%);
}

.gallery {
  background: -webkit-linear-gradient(90deg, #090f30, #000000, #190631);
  background: linear-gradient(90deg, #090f30, #000000, #190631);
  padding: 0 0 8.75em 0;
}

.gallery__title {
  @include font-patterns(#f0f0f0, "HandveticaNeue", 2.5rem, normal, 500, 170%);
  padding: 0.5em 0;
  text-align: center;
  text-transform: uppercase;
}

.gallery__container {
  position: relative;
}

.gallery__list-item {
  display: none;
  text-align: center;
}

.cursor {
  cursor: pointer;
}

.gallery__arrow-wrapper {
  position: relative;
}

.prev,
.next {
  cursor: pointer;
  position: absolute;
  width: auto;
  padding: 0.5em;
  margin-top: -20em;
  color: white;
  font-weight: bold;
  font-size: 1.25rem;
  border-radius: 25%;
  user-select: none;
  -webkit-user-select: none;
}

.next {
  right: 0;
}

.prev:hover,
.next:hover {
  color: #000;
  background-color: #f0f0f0;
}

.prev:active,
.next:active {
  background-color: gray;
}

.gallery__caption-wrapper {
  margin: 0.5em 0;
  padding: 0;
}

.gallery__wrapper-desc {
  @include font-patterns(#f0f0f0, "Inter-Regular", 1rem, normal, 500, 170%);
  margin: 1em auto;
}

.caption-container {
  text-align: center;
}

.gallery__row:after {
  content: "";
  display: table;
  clear: both;
}

.gallery__column {
  float: left;
  width: 16.66%;
}

.gallery__column-demo {
  opacity: 0.6;
}

.active,
.gallery__column-demo:hover {
  opacity: 1;
}

.modal__background {
  background: rgba(0, 0, 0, 0.8);
  bottom: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  right: 0;
  top: 0;
  transition: 0.35s;
  z-index: 2;

  &:target {
    opacity: 1;
    pointer-events: auto;
  }
}

.background {
  display: flex;
  background: #f0f0f0;
  width: 100vw;
  height: 100vh;
}

.coaches__modal-wrapper,
.path__modal-wrapper {
  background: #fff;
  border-radius: 5px;
  box-shadow: 0 5px 0 rgba(0, 0, 0, 0.1);
  padding: 1rem;
  position: relative;
  z-index: 4;

  img {
    display: block;
    margin: 0 auto;
  }

  @media screen and (max-width: 699px) {
    margin: 6rem auto;
  }

  @media screen and (min-width: 700px) {
    margin: 4rem auto;
    max-width: 500px;
    min-height: 500px;
  }

  .modal__wrapper-close {
    color: rgba(0, 0, 0, 0.5);
    font-size: 35px;
    position: absolute;
    right: 1rem;
    text-decoration: none;
    top: 0rem;
    transition: 0.25s;

    &:hover {
      color: rgba(0, 0, 0, 1);
    }
  }
}

.coaches__modal-title,
.path__modal-title {
  @include font-patterns(#000, "Inter-Regular", 2rem, normal, 600, 170%);
  margin: 0 0 1rem;
  text-align: center;
}

.coaches__modal-subtitle,
.path__modal-subtitle {
  @include font-patterns(#000, "Inter-Regular", 1rem, normal, 400, 170%);
  margin: 1em;
  text-align: center;
}

.success {
  width: 100%;
}

.contacts {
  background: -webkit-linear-gradient(90deg, #090f30, #000000, #190631);
  background: linear-gradient(90deg, #090f30, #000000, #190631);
  padding: 0 0 8em 0;
}

.contacts__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.contacts__title {
  @include font-patterns(#f0f0f0, "HandveticaNeue", 2.25rem, normal, 500, 170%);
  width: 100%;
  padding: 1em 0;
  text-align: center;
  text-transform: uppercase;
}
.contacts__list {
}
.contacts__list-item {
  @include font-patterns(#f0f0f0, "Inter-Regular", 1.5rem, normal, 400, 62%);
  margin: 1.5em;
}
.contacts__item-link {
  display: inline-block;
  color: #f0f0f0;
  text-decoration: underline;
}
.list__item {
}
.contacts__map {
  width: 500px;
  min-width: 300px;
  height: 350px;
}

.schedule,
.prices {
  background: -webkit-linear-gradient(90deg, #090f30, #000000, #190631);
  background: linear-gradient(90deg, #090f30, #000000, #190631);
}

.schedule__title,
.prices__title {
  @include font-patterns(#f0f0f0, "HandveticaNeue", 2.25rem, normal, 500, 170%);
  width: 100%;
  padding: 1em 0;
  text-align: center;
  text-transform: uppercase;
}

.schedule__table,
.prices__table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  height: auto;
}
.schedule__table,
.schedule__table td,
.schedule__table th,
.prices__table,
.prices__table td,
.prices__table th {
  border: 1px solid #595959;
}

.schedule__row > *:hover,
.prices__row > *:hover {
  color: #000;
  background-color: #f0f0f0;
}

.schedule__row-item,
.prices__row-item {
  @include font-patterns(#f0f0f0, "TheCrewPro", 1.25rem, normal, 500, 170%);
  text-align: center;
}

.schedule__row-day,
.prices__row-title {
  @include font-patterns(#f0f0f0, "Poppins-Bold", 1rem, normal, 600, 170%);
  text-align: center;
}

.schedule__row-time,
.prices__row-price {
  @include font-patterns(#f0f0f0, "Inter-Regular", 1rem, normal, 400, 170%);
  text-align: center;
}

.schedule__table th,
.prices__table th {
  @include font-patterns(#f0f0f0, "Inter-Regular", 1rem, normal, 400, 170%);
  padding: 3px;
  width: 30px;
  height: 35px;
  text-align: center;
}

.schedule__table th,
.prices__table th {
  background: #4e6982;
  color: #fff;
}

.schedule__image {
  img {
    display: block;
    margin: 0 auto;
    padding: 1.95em 0 0 0;
    filter: invert(1);
  }
}

.prices__image {
  img {
    display: block;
    margin: 0 auto;
    padding: 3.95em 0 0 0;
    filter: invert(1);
  }
}

.about,
.news {
  background: -webkit-linear-gradient(90deg, #090f30, #000000, #190631);
  background: linear-gradient(90deg, #090f30, #000000, #190631);
}
.about__container,
.news__container {
  img {
    display: block;
    margin: 0 auto;
    padding: 0 0 1.25em 0;
  }
}

.about__title,
.news__title {
  @include font-patterns(#f0f0f0, "HandveticaNeue", 2.25rem, normal, 500, 170%);
  width: 100%;
  padding: 1em 0;
  text-align: center;
  text-transform: uppercase;
}

.about__subtitle,
.news__subtitle {
  @include font-patterns(#f0f0f0, "Inter-Regular", 1rem, normal, 400, 170%);
  padding: 1em 0;
  text-align: center;
}

/*  MEDIA STYLES WILL BE HERE  */
@media (max-width: 1200px) {
  .mission__subtitle {
    max-width: 75%;
  }
}

@media (max-width: 1024px) {
  .page {
    font-size: 14px;
  }

  .prev,
  .next {
    margin-top: 0.3em;
  }
}

@media (max-width: 1024px) {
  .page {
    font-size: 14px;
  }

  .prev,
  .next {
    margin-top: 0.3em;
  }
}

@media (max-width: 911px) {
  .motivation__picture {
    padding: 0;
  }

  .motivation__container {
    min-height: calc(92vh - 104px);
  }

  .motivation__desc {
    text-align: center;
    row-gap: 3em;
  }
}

@media (max-width: 768px) {
  .motivation__container {
    flex-wrap: wrap;
  }

  .motivation__desc {
    row-gap: 2em;
    order: 1;
  }

  .path__title,
  .mission__title,
  .coaches__title,
  .gallery__title {
    padding: 2em 0 0.5em 0;
  }

  .mission,
  .path,
  .coaches {
    padding: 0;
  }

  .path__list {
    flex-wrap: wrap;
    row-gap: 2em;
  }

  .gallery {
    padding: 0 0 70px 0;
  }
}

@media (max-width: 500px) {
  .page {
    font-size: 13px;
  }

  .motivation__desc-title {
    font-size: 4.5rem;
  }

  .coaches__modal-title {
    @include font-patterns(#000, "Inter-Regular", 1.75rem, normal, 600, 170%);
  }
}
