/*  FOOTER STYLES WILL BE HERE  */
.footer {
    background: -webkit-linear-gradient(90deg, #090f30,#000000,#190631);
    background: linear-gradient(90deg, #090f30,#000000,#190631);
    border-top: 1px solid #f0f0f0;
}

.footer__container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 1.5em!important;
}

.footer__nav {

}

.footer__nav-title {
    @include font-patterns(#f0f0f0, "HandveticaNeue", 1.25rem, normal, 400, 91%);
    padding: 0 0 .5em 0;
    text-transform: uppercase;
}

.footer__nav-list {

}

.footer__list-item {
    margin: 1em 0;
}

.footer__item-link {
    @include font-patterns(#ff7846, "TheCrewPro", 1.25rem, normal, 400, 62%);
    cursor: pointer;
    transition: 0.2s;

    &:hover {
        color: #fff;
        transition: 0.2s;
    }
}

.footer__contacts-title {
    @include font-patterns(#f0f0f0, "HandveticaNeue", 1.25rem, normal, 400, 100%);
    padding: 0 0 .5em 0;
    text-transform: uppercase;

}
.footer__contacts-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-height: 140px;
    flex-wrap: wrap;
    align-items: center;
}
.footer__contacts-link {
    img {
        transition: 0.2s;
    }
    &:hover {
        cursor: pointer;
        img {
            transform: rotateX(360deg);
            transition: 0.2s;
        }
    }
}
.footer__worktime {

}

.footer__worktime-title {
    @include font-patterns(#f0f0f0, "HandveticaNeue", 1.25rem, normal, 400, 100%);
    padding: 0 0 .5em 0;
    text-transform: uppercase;
}

.footer__worktime-item,
.footer__address-item {
    @include font-patterns(#f0f0f0, "Inter-Regular", 1rem, normal, 500, 100%);
    margin: 1em 0;
}

.footer__address-title {
    @include font-patterns(#f0f0f0, "HandveticaNeue", 1.25rem, normal, 500, 100%);
    padding: 0 0 .5em 0;
    text-transform: uppercase;
}

@media (max-width: 768px) {
    .footer__container {
        justify-content: space-around;
        column-gap: 12px;
        row-gap: 2em;
        text-align: center;
      }
}

@media (max-width: 542px) {
    .footer__nav {
        order: 0;
    }

    .footer__contacts {
        order: 1;
    }

    .footer__contacts-list {
        max-height: 130px;
    }

    .footer__nav,
    .footer__contacts,
    .footer__worktime,
    .footer__address {
        flex: 0 0 45%;
        text-align: center;
    }

}